.parkForm{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    text-align: center;
    background-color: #262a3b;
    -webkit-box-shadow: 9px 9px 10px -8px rgba(0,0,0,0.75);
-moz-box-shadow: 9px 9px 10px -8px rgba(0,0,0,0.75);
box-shadow: 9px 9px 10px -8px rgba(0,0,0,0.75);
padding: 20px;
color:white;
font-size: 20px;
position: relative;
}

.parkForm input{
    width: 300px;
    height: 30px;
    display: block;
    padding: 6px;
    color:white;
    font-size: 16px;
    border-radius: 4px;
    border:1px solid white;
    outline: none;
    background-color: #262a3b;
    margin-top: 10px;
    background-color: none;
}

.parkForm button{
    padding:8px 10px;
    margin: 16px;
    outline: none;
    border:none;
    color: white;
    font-size: 15px;
    background-color: #5370f0;
}

.parkForm img{
    position: absolute;
    max-width: 60px;
    max-height: 60px;
    top:-50px;
    left:42%;
    border-radius: 100%;
    background-color: aliceblue;
    padding: 10px;
}

.parkForm label img{
    position:static;
    margin: 0px 6px;
    display: inline;
    max-width: 20px;
    max-height: 20px;
    border-radius: 0;
    padding: 0px;
    background-color: #262a3b;
}

.parkForm label p{
    display: inline;
}

.parkForm label div{
    margin-top: 30px;
}