.App{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap:60px;
  margin-top: 10%;
}

.logo img {
  position: absolute;
  max-width: 80px;
  max-height: 80px;
  top:0;
  left:50%;
}

.parkingBg{
  background-color: aliceblue;
}

.phoneParkimg{
  position: absolute;
  top:30%;
  z-index: -1;
  opacity: 0.5;
}

.checkout{
  background-color: aliceblue;
  position: relative;
}
.checkout form{
  background-color: #262a3b;
  margin-top: 6px;
    -webkit-box-shadow: 9px 9px 10px -8px rgba(0,0,0,0.75);
-moz-box-shadow: 9px 9px 10px -8px rgba(0,0,0,0.75);
box-shadow: 9px 9px 10px -8px rgba(0,0,0,0.75);
}

.checkout input{
  width: 200px;
  height: 20px;
  border:none;
  padding: 6px;
  color:white;
  border-radius: 4px;
  border:1px solid white;
  outline: none;
  background-color: #262a3b;
  margin: 10px;
}

.checkout button{
  font-size: 12px;
  color:white;
  border:none;
  outline: none;
  padding: 6px 8px;
  background-color:#5370f0;
  border-radius: 5px;
  margin: 0px 10px;
}

.checkout img{

  position: absolute;
  max-width: 40px;
  max-height: 40px;
  top:-50px;
  left:42%;
  border-radius: 100% 100% 100% 100%;
  background-color: aliceblue;
  padding: 10px;
}

.error{
  background-color: #f33a11;
  color:white;

  text-align: center;
  font-size: 18px;
  -webkit-box-shadow: 9px 9px 10px -8px rgba(0,0,0,0.75);
-moz-box-shadow: 9px 9px 10px -8px rgba(0,0,0,0.75);
box-shadow: 9px 9px 10px -8px rgba(0,0,0,0.75);
}

.onDeck{
  position: absolute;
  top:200%;
  left: 50%;
  text-align: center;
  background-color: #262a3b;
  -webkit-box-shadow: 9px 9px 10px -8px rgba(0,0,0,0.75);
-moz-box-shadow: 9px 9px 10px -8px rgba(0,0,0,0.75);
box-shadow: 9px 9px 10px -8px rgba(0,0,0,0.75);
color:white;
border-radius: 10px;
}
.onDeck .heading {
  border-radius: 10px;
  background-color: aliceblue;
  color: #2e211e;
  font-weight: 500;
}

.count{
  color:#5370f0;
  font-size: 22px;
}
